<template>
  <div class="form__counter">
    <button type="button" aria-label="Убрать один товар" @click.prevent="reduce()">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-minus"></use>
      </svg>
    </button>

    <input type="text" v-model.number="value" @input="userInput()" name="count">

    <button type="button" aria-label="Добавить один товар" @click.prevent="increment()">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-plus"></use>
      </svg>
    </button>
  </div>
</template>

<script>
  export default {
    props: ['value'],
    methods: {
      reduce() {
        if (this.value > 1) this.$emit('input', this.value - 1);
      },
      increment() {
        this.$emit('input', this.value + 1);
      },
      userInput() {
        this.$emit('input', this.value);
      }
    }
  };
  
</script>