<template>
  <router-link class="header__cart" aria-label="Корзина с товарами" :to="{name: 'cart'}">
    <svg width="30" height="21" fill="currentColor">
      <use xlink:href="#icon-cart"></use>
    </svg>
    <span v-if="this.cartLoading">
      <img src='img/spinning-arrow.gif' class="header__preloder" alt="Загрузка товаров...">
    </span>
    <span v-else-if="this.cartProducts" class="header__count" aria-label="Количество товаров">{{ this.cartProducts.length }}</span>
  </router-link>
</template>

<script>
import { mapState } from 'vuex';
  export default {
    computed: {
      ...mapState(['cartLoading', 'cartProducts'])
    }
  };
</script>