<template>
  <div class="cart__block">
    <ul
      class="cart__orders"
      v-for="item in products"
      :key="item.productId"
      :item="item"
    >
      <li class="cart__order">
        <h3>{{ item.product.title }}</h3>
        <b>{{ item.product.price | numberFormat }} ₽</b>
        <span>{{ item.productId }}</span>
      </li>
    </ul>

    <div class="cart__total">
      <p>Доставка: <b>500 ₽</b></p>
      <p>Итого: <ProductCount :countProducts="products.length"></ProductCount> на
        сумму <b>{{ (totalPrice + 500) | numberFormat }} ₽</b>
      </p>
    </div>

    <slot></slot>
  </div>
</template>

<script>
  import ProductCount from "@/components/ProductCount.vue";
  import numberFormat from "@/helpers/numberFormat";

  export default {
    props: ['products', 'totalPrice'],
    components: {ProductCount},
    filters: { numberFormat }
  }
</script>