<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html"> Каталог </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link"> Корзина </a>
        </li>
      </ul>
      <h1 class="content__title">Корзина</h1>
      
      <ProductCount :countProducts="products.length" class="content__info"></ProductCount>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#">
        <div class="cart__field">
          
          <ul class="cart__list">
            <li v-if="this.cartLoading" style="text-align: center;">
              <h2>Загрузка товаров</h2>
              <img src='img/circles-menu-3.gif' alt="Загрузка товаров...">
            </li>
            <li v-else-if="this.cartLoadingFailed" style="text-align: center;">
              <h2>Произошла ошибка при загрузке товаров </h2>
              <button @click.prevent="this.loadCart" class="button--reset">Попробовать еще раз
                <svg xmlns="http://www.w3.org/2000/svg" style="vertical-align: middle;" x="0px" y="0px" width="24" height="24" viewBox="0 0 24 24">
              <path d="M 2 2 L 4.9394531 4.9394531 C 3.1262684 6.7482143 2 9.2427079 2 12 C 2 17.514 6.486 22 12 22 C 17.514 22 22 17.514 22 12 C 22 6.486 17.514 2 12 2 L 12 4 C 16.411 4 20 7.589 20 12 C 20 16.411 16.411 20 12 20 C 7.589 20 4 16.411 4 12 C 4 9.7940092 4.9004767 7.7972757 6.3496094 6.3496094 L 9 9 L 9 2 L 2 2 z"></path>
              </svg>
              </button>
            </li>
            <li v-else-if="products">
              <CartItem v-for="item in products" :key="item.productId" :item="item"/>
            </li>
          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
          </p>
          <p class="cart__price">Итого: <span>{{ totalPrice | numberFormat }} ₽</span></p>
          <router-link tag="button" :to="{name: 'order'}" :disabled="products.length === 0" class="cart__button button button--primery" type="button">
            Оформить заказ
          </router-link>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
  import numberFormat from '@/helpers/numberFormat';
  import { mapGetters, mapActions, mapState } from 'vuex';
  import CartItem from '@/components/CartItem.vue';
  import ProductCount from '@/components/ProductCount.vue';

  export default {
    filters: { numberFormat },
    computed: {
      ...mapGetters({products: 'cartDetailProducts', totalPrice: 'cartTotalPrice'}),
      ...mapState(['cartLoading', 'cartLoadingFailed'])
    },
    methods: {
      ...mapActions(['loadCart']),
    },
    created() {
      this.loadCart()
    },
    components: { CartItem, ProductCount }
  }
</script>