<template>
    <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      
        <ProductCount :countProducts="countProducts" class="content__info"></ProductCount>
    </div>

    <div class="content__catalog">
        <ProductFilter :price-from.sync="filterPriceFrom" :price-to.sync="filterPriceTo" :category-id.sync="filterCategoryId" :color-id.sync="filterColorId"></ProductFilter>
        <section class="catalog">
          <div v-if="productsLoading" style="text-align: center;">
            <h2>Загрузка товаров</h2>
            <img src='img/circles-menu-3.gif' alt="Загрузка товаров...">
          </div>
          <div v-else-if="productsLoadingFailed" style="text-align: center;">
            <h2>Произошла ошибка при загрузке товаров </h2>
            <button @click.prevent="loadProducts" class="button--reset">Попробовать еще раз
              <svg xmlns="http://www.w3.org/2000/svg" style="vertical-align: middle;" x="0px" y="0px" width="24" height="24" viewBox="0 0 24 24">
              <path d="M 2 2 L 4.9394531 4.9394531 C 3.1262684 6.7482143 2 9.2427079 2 12 C 2 17.514 6.486 22 12 22 C 17.514 22 22 17.514 22 12 C 22 6.486 17.514 2 12 2 L 12 4 C 16.411 4 20 7.589 20 12 C 20 16.411 16.411 20 12 20 C 7.589 20 4 16.411 4 12 C 4 9.7940092 4.9004767 7.7972757 6.3496094 6.3496094 L 9 9 L 9 2 L 2 2 z"></path>
            </svg>
            </button>
          </div>
          <ProductList v-else-if="products" :products="products"></ProductList>
          <BasePagination v-model="page" :count="countProducts" :per-page="productsPerPage"></BasePagination>
        </section>
    </div>
  </main>
</template>

<script>
import ProductList from '@/components/ProductList';
import BasePagination from '@/components/BasePagination';
import ProductFilter from '@/components/ProductFilter';
import ProductCount from '@/components/ProductCount.vue';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

export default {
  components: { ProductList, BasePagination, ProductFilter, ProductCount },
  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterColorId: 0,

      page: 1,
      productsPerPage: 3,
      productsData: null,

      productsLoading: false,
      productsLoadingFailed: false,
    }
  },
  computed: {
    products() {
      return this.productsData 
        ? this.productsData.items.map(product => {
          return {
            ...product,
            image: product.image.file.url
          }
        })
        : [];
    },
    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0;
    }
  },
  methods: {
    loadProducts(){
      this.productsLoading = true;
      this.productsLoadingFailed = false;
      
      return (new Promise(resolve => setTimeout(resolve, 1000)))
      .then(() => {
        return axios
          .get(API_BASE_URL + `/api/products`, {
            params: {
              page: this.page,
              limit: this.productsPerPage,
              categoryId: this.filterCategoryId,
              colorId: this.filterColorId,
              minPrice: this.filterPriceFrom,
              maxPrice: this.filterPriceTo
            }
          })
          .then(response => this.productsData = response.data)
          .catch(() => this.productsLoadingFailed = true)
          .then(() => this.productsLoading = false);
      })
    }
  },
  watch: {
    page(){
      this.loadProducts();
    },
    filterPriceFrom(){
      this.loadProducts();
    },
    filterPriceTo(){
      this.loadProducts();
    },
    filterCategoryId(){
      this.loadProducts();
    },
    filterColorId(){
      this.loadProducts();
    },
  },
  created(){
    this.filterCategoryId = this.$route.query.categoryId || 0;
    this.loadProducts();
  }
}
</script>
