<template>
  <div class="content" v-if="!orderInfo" style="text-align: center;">
    <h2>Оформляем заказ</h2>
    <img src='img/dancing-kitty.gif' alt="Оформляем заказ...">
  </div>
  <main class="content container" v-else>
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html"> Каталог </a>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'cart'}"> Корзина </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link"> Оформление заказа </a>
        </li>
      </ul>

      <h1 class="content__title">Заказ оформлен <span>№ 23621</span></h1>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
            Благодарим за&nbsp;выбор нашего магазина. На&nbsp;Вашу почту придет
            письмо с&nbsp;деталями заказа. Наши менеджеры свяжутся с&nbsp;Вами
            в&nbsp;течение часа для уточнения деталей доставки.
          </p>

          <ul class="dictionary">
            <li class="dictionary__item">
              <span class="dictionary__key"> Получатель </span>
              <span class="dictionary__value">
                {{ this.orderInfo.name }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key"> Адрес доставки </span>
              <span class="dictionary__value">
                {{ this.orderInfo.address }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key"> Телефон </span>
              <span class="dictionary__value"> {{ this.orderInfo.phone }} </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key"> Email </span>
              <span class="dictionary__value"> {{ this.orderInfo.email }} </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key"> Способ оплаты </span>
              <span class="dictionary__value"> картой при получении </span>
            </li>
          </ul>
        </div>

        <CartCheck :products="orderInfo.basket.items" :totalPrice="orderInfo.totalPrice">
        </CartCheck>
      </form>
    </section>
  </main>
</template>

<script>
  import { mapState, mapActions } from 'vuex';
  import CartCheck from '@/components/CartCheck.vue';

  export default {
    computed: {
      ...mapState(['orderInfo'])
    },
    methods: {
      ...mapActions(['loadOrderInfo']),
    },
    components: { CartCheck },
    created(){
      
      if(this.orderInfo && this.orderInfo.id === this.$route.params.id) {
        return;
      }
      
      this.loadOrderInfo(this.$route.params.id);
    },
  }
  
</script>